    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data(){
      return{
      }
    }
  };
</script>
<style lang='scss' scoped>
.payerror-box{
  text-align: center;
  background: #fff;
  padding: 20px 0;
  margin-top: 40px;
  border-top: 1px solid #ddd;
  img{
    width: 6rem;
    height:6rem;
  }
  .p2{font-size: 12px;color: #999;}
  .p3{font-size: 14px;}
  
}
.closepage{
  text-align: center;
  margin-top: 40px;
  .closeIcon{font-size: 40px;color: #6CBAFB;}
  .p4{font-size: 12px;}
}
</style>


